<template>
  <div class="container bg-black">
    <div class="input-container">
      <input
        id="searchBox"
        type="text"
        placeholder="Enter location or postcode"
        v-model="postCode"
      />
      <input id="submitButton" type="button" value="Search" />
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "PostCodeSearch",
  data() {
    return {
      postCode: "",
    };
  },

  mounted() {
    let that = this;

    document
      .getElementById("searchBox")
      .addEventListener("keyup", function(event) {
        if (event.keyCode === 13) {
          event.preventDefault();
          that.getPostcodeData();
        }
      });

    document
      .getElementById("submitButton")
      .addEventListener("click", function() {
        that.getPostcodeData();
      });
  },

  methods: {
    hasNumber(myString) {
      return /\d/.test(myString);
    },

    getPostcodeData() {

      if(this.hasNumber(this.postCode)){
        var postCodeURL =
          "https://maps.googleapis.com/maps/api/geocode/json?address=" +
          this.postCode +
          "&key=" +
          this.$store.state.gMapKey;

        axios
          .get(postCodeURL)
          .then((response) => {
            let lat = response.data.results[0].geometry.location.lat;
            let lng = response.data.results[0].geometry.location.lng;
            
            let urlLocal =
              "https://api.openweathermap.org/data/2.5/weather?lat=" +
              lat +
              "&lon=" +
              lng +
              "&appid=" +
              this.$store.state.appid;

            let localObj = {
              id: 'local',
              url: urlLocal
            }

            this.$store.dispatch("getAppData", localObj);

            let urlForecast = "https://api.openweathermap.org/data/2.5/forecast/?lat=" +
              lat +
              "&lon=" +
              lng +
              "&appid=" +
              this.$store.state.appid;;

            let forecastObj = {
              id: 'forecast',
              url: urlForecast
            }
            
            this.$store.dispatch('getAppData', forecastObj);
        })
        .catch((error) => {
          console.log(error);
        });

      } else {

        let localUrl = 'https://api.openweathermap.org/data/2.5/weather?q=' + this.postCode + "&appid=" + this.$store.state.appid;

        let localObj = {
          id: 'local',
          url: localUrl
        }

        let forecastUrl = 'https://api.openweathermap.org/data/2.5/forecast?q=' + this.postCode + "&appid=" + this.$store.state.appid;

        let forecastObj = {
          id: 'forecast',
          url: forecastUrl
        }
        
        this.$store.dispatch('getAppData', forecastObj);
        this.$store.dispatch('getAppData', localObj);
      }

    },
  },
};
</script>

<style scoped lang="scss">
.container {
  padding: 15px auto 0;
  height: 60px;
  width: 100%;
  @media screen and (max-width: 567px) {
    height: 50px;
  }
}

.input-container {
  max-width: 600px;
  padding-top: 5px;
  margin: 0 auto;

  @media screen and (max-width: 567px) {
    padding-top: 0;
  }
}

#searchBox,
#submitButton {
  height: 40px;
  font-size: 15px;
  border: none;
}

#searchBox {
  display: inline-block;
  width: Calc(100% - 110px);
  margin: 5px 0 5px 5px;
  padding: 10px;
  border-radius: 5px 0 0 5px;
}

#submitButton {
  display: inline-block;
  width: 100px;
  margin: 5px 5px 5px 0;
  border-radius: 0 5px 5px 0;
}
</style>
